import { LitElement, customElement, html } from '@umbraco-cms/backoffice/external/lit';
import SyncExporterWorkspaceContext from './workspace.context';
import { UmbElementMixin } from '@umbraco-cms/backoffice/element-api';

@customElement('usync-exporter-element')
export class uSyncExporterElement extends UmbElementMixin(LitElement) {
	//@ts-ignore - setup to provide the context from this element
	#context: SyncExporterWorkspaceContext;

	constructor() {
		super();
		this.#context = new SyncExporterWorkspaceContext(this);
	}

	render() {
		return html`<umb-workspace-editor .enforceNoFooter=${true}>
			<div slot="header">
				<strong
					><umb-localize key="uSyncExporter_name">uSync Exporter</umb-localize></strong
				><br />
				<em>
					<umb-localize key="uSyncExporter_description"
						>Export all the things</umb-localize
					>
				</em>
			</div>
		</umb-workspace-editor>`;
	}
}

export default uSyncExporterElement;

declare global {
	interface HTMLElementTagNameMap {
		'usync-exporter-element': uSyncExporterElement;
	}
}
